import { Container, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import Onboarding from './components/Onboarding';
import Horoscope from './components/Horoscope';


function App() {

  const [sign, setSign] = useState<string>(localStorage.getItem('sign') || "")
  const [name, setName] = useState<string>(localStorage.getItem('name') || "")
  const [onboardingDone, setOnbDone] = useState<boolean>(JSON.parse(localStorage.getItem('onboardingDone') || "false"))

  useEffect(() => {
    if (sign) localStorage.setItem('sign', sign);
  }, [sign]);

  useEffect(() => {
    if (name) localStorage.setItem('name', name);
  }, [name]);

  useEffect(() => {
    localStorage.setItem('onboardingDone', JSON.stringify(onboardingDone));
  }, [onboardingDone]);
  
  return (
    <Container maxWidth="sm">
      { (!(sign && name) && !onboardingDone)
        ? <Onboarding setSign={setSign} setName={setName} setOnbDone={setOnbDone} />
        : (onboardingDone)
          ? <Horoscope sign={sign} name={name} />
          : <Typography color='error'>Ooops, something went wrong [App]</Typography>
      }
    </Container>
  );
}

export default App;
