import { Box, Button, Container, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent, Tab, Tabs, Typography } from "@mui/material"
import { useEffect, useRef, useState } from "react";
import { zodiacSigns } from "./SignsData";

interface Props {
    sign: string,
    name: string,
}

const Horoscope: React.FC<Props> = ({sign, name}): React.ReactElement => {

    const [activeTab, setActiveTab] = useState<number>(1)
    const [period, setPeriod] = useState<string>("TODAY")
    const [signSelect, setSignSelect] = useState<string>(sign)
    const [open, setOpen] = useState<boolean>(false)
    const [dataAPI, setDataAPI] = useState({
        date: "",
        horoscope_data: ""
    })
    const dataRequest: React.MutableRefObject<boolean> = useRef(false)
    const [error, setError] = useState<string | null>(null)

    const dataFetch = async (): Promise<void> => {
        try {
            const fetchCall: Response = await fetch(`https://horoscope-app-api.vercel.app/api/v1/get-horoscope/daily?sign=${signSelect}&day=${period}`)
            const data: any = await fetchCall.json()
            setDataAPI({date: data.data.date, horoscope_data: data.data.horoscope_data})
            setError(null)
        } catch {
            setError('Oops, something went wrong [API]')
        }
    }

    useEffect (() => {
        if (signSelect) {dataFetch()}
        if (!dataRequest) {dataFetch()}
        return () => {dataRequest.current = true}
    }, [signSelect, period])

    const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
        setActiveTab(newValue)
        if (newValue === 0) {setPeriod("YESTERDAY")}
        if (newValue === 1) {setPeriod("TODAY")}
        if (newValue === 2) {setPeriod("TOMORROW")}
        
    }

    return (
        <Container maxWidth="sm" sx={{marginTop:"20px", display:"flex", flexDirection: "column", alignItems:"center"}}>

            <Typography variant="caption" color="secondary" sx={{ marginTop: "10px" }}>Your sign is: <strong>{sign}</strong></Typography>

            <img src={`/${sign}.png`} alt={`${sign} symbol`} style={{maxHeight:"200px"}}/>

            <Typography variant="h4" sx={{marginTop:"20px"}}>
                Hello {name}
            </Typography>
            
            <Typography variant="h6" sx={{marginTop:"5px"}} align="center">
                let's see what the stars have for {signSelect} ✨
            </Typography>

            <Container sx={{display:"flex", flexDirection:"row", justifyContent:"center", alignItems:"baseline", gap:"8px"}}>

                <Typography color="disabled" >or</Typography>

                <Button
                    variant="outlined"
                    color="secondary"
                    size="small"
                    sx={{marginTop:"10px", borderRadius:"30px"}}
                    onClick={() => setOpen(true)}
                    >
                    Check another sign
                </Button>

                <Dialog open={open} onClose={() => setOpen(false)}>

                    <DialogTitle>Choose zodiac sign</DialogTitle>

                    <DialogContent>
                        <FormControl sx={{minWidth:"100px", marginTop:"5px"}} fullWidth>

                            <InputLabel id="zodiac">another sign</InputLabel>

                            <Select
                                value={signSelect}
                                label="another sign"
                                labelId="zodiac"
                                onChange={(e: SelectChangeEvent) => setSignSelect(e.target.value)}
                                >
                                {zodiacSigns.map((signs: Zodiac, idx: number) => { return(<MenuItem key={idx} value={signs.sign}>{signs.sign}</MenuItem>) })}
                            </Select>
                            
                        </FormControl>
                    </DialogContent>

                    <DialogActions>
                        <Button onClick={() => setOpen(false)}>Cancel</Button>
                        <Button onClick={() => setOpen(false)}>View horoscope</Button>
                    </DialogActions>

                </Dialog>

            </Container>

            <Tabs 
                value={activeTab} 
                onChange={handleTabChange} 
                variant="fullWidth"
                sx={{marginTop: "20px"}}
                textColor="secondary"
                indicatorColor="secondary"
                centered>
                <Tab label="Yesterday"/>
                <Tab label="Today"/>
                <Tab label="Tomorrow"/>
            </Tabs>

            <Box sx={{marginTop: 3}}>
                { error
                    ? <Typography color="error">{error}</Typography>
                    :<>
                        {activeTab === 0 && (
                            <Box>
                                <Typography variant="overline">{dataAPI.date}</Typography>
                                <Typography>{dataAPI.horoscope_data}</Typography>
                            </Box>
                        )}

                        {activeTab === 1 && (
                            <Box>
                                <Typography variant="overline">{dataAPI.date}</Typography>
                                <Typography>{dataAPI.horoscope_data}</Typography>
                            </Box>
                        )}

                        {activeTab === 2 && (
                            <Box>
                                <Typography variant="overline">{dataAPI.date}</Typography>
                                <Typography>{dataAPI.horoscope_data}</Typography>
                            </Box>
                        )}
                    </>
                }
            </Box>

        </Container>
    )
}

export default Horoscope