import { Button, Container, FormControl, InputLabel, MenuItem, Select, SelectChangeEvent, TextField, Typography } from "@mui/material"
import { zodiacSigns } from "./SignsData"
import { DatePicker, LocalizationProvider } from "@mui/x-date-pickers"
import React, { useState } from "react"
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns'
import { fi } from "date-fns/locale"

interface Props {
    setSign: (arg0: string) => void,
    setName: (arg0: string) => void,
    setOnbDone: (arg0: boolean) => void,
}

interface Errors {
    birthdate?: string,
    sign?: string
}

const Onboarding: React.FC<Props> = ({setSign, setName, setOnbDone}): React.ReactElement => {

    const [birthdate, setBirthdate] = useState<Date>()
    const [signTemp, setSignTemp] = useState<string>("")
    const [nameTemp, setNameTemp] = useState<string>("")
    const [errorMsgs, setErrorMsgs] = useState<Errors>({})
    const [inputChange, setInputChange] = useState<boolean>(false)

    const handleForm = (e: React.FormEvent) :void => {
        e.preventDefault()

        let errors: Errors = {}
        if (!birthdate && inputChange === false) {
            errors = {birthdate: "Enter your birthdate or select sign manually so we can figure out your zodiac sign."}
        }
        if (birthdate && !signTemp) {
            errors = {sign: "Unable to determine your zodiac sign."}
        }
        if (!signTemp && inputChange === true) {
            errors = {sign: "Select sign or enter your birthdate so we can figure out your zodiac sign."}
        }

        if (Object.entries(errors).length > 0) {
            setErrorMsgs({...errors})
        } else {
            setErrorMsgs({})
            setSign(signTemp)
            setName(nameTemp || "")
            setOnbDone(true)
        }
    }

    const inputHandler = ():void => {
        if (inputChange === true) { setBirthdate(undefined) }
        if (inputChange === false) { setSignTemp("") }
    }

    const handleZodiacSign = (date: Date | null): string | null => {
        if (!date) return null
        const month = date.getMonth()
        const day = date.getDate()
    
        for (const { sign, start, end } of zodiacSigns) {
          const startMonth = new Date(start).getMonth()
          const startDay = new Date(start).getDate()
          const endMonth = new Date(end).getMonth()
          const endDay = new Date(end).getDate()
    
          if (
            (month === startMonth && day >= startDay) ||
            (month === endMonth && day <= endDay) ||
            (startMonth > endMonth && (month === startMonth || month === endMonth))
          ) { return sign }
        }
        return null
    }

    return (
        <Container maxWidth="sm" sx={{marginTop:"20px", display:"flex", flexDirection: "column", alignItems:"center"}}>

            <img src="/wizard.png" alt="wizard" style={{maxHeight:"200px"}}/>

            <Typography variant="h5" sx={{marginTop:"20px"}}>Welcome to Luna! </Typography>
            <Typography>Discover what the universe has in store for you today ✨
                <br /><br />
                Fill in a few details to make your horoscope more personal:
            </Typography>

            <form
                onSubmit={handleForm}
                style={{marginTop:"20px", display:"flex", flexDirection: "column", gap:"10px", alignItems:"center", width: "70%"}}
            >
                <TextField value={nameTemp} label="Your name" onChange={(e) => setNameTemp(e.target.value)} fullWidth/>

                { (inputChange === false)
                    ?   <>
                        <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
                        <DatePicker
                            label="Birthdate"
                            value={birthdate}
                            onChange={(date: Date | null) => {
                                setBirthdate(date!);
                                const foundSign = handleZodiacSign(date);
                                if (foundSign) {
                                    setSignTemp(foundSign)
                                }
                                }}
                            sx={{ width: "100%" }} />
                        </LocalizationProvider>
                            {Boolean(errorMsgs.birthdate)
                                ? <Typography color="error">{errorMsgs.birthdate}</Typography>
                                : null
                            }

                        {(birthdate && signTemp)
                            ? <Typography variant="caption" sx={{ marginTop: "10px" }}>Your sign is: <strong>{signTemp}</strong></Typography>
                            : null
                        }
                            {Boolean(errorMsgs.sign)
                                ? <Typography color="error">{errorMsgs.sign}</Typography>
                                : null
                            }

                        <Button size="small" onClick={() => {setInputChange(true); inputHandler()}}>or select zodiac sign manually</Button>
                        </>
                    :   <>
                        <FormControl fullWidth>
                            <InputLabel id="zodiac">Zodiac sign</InputLabel>
                            <Select
                                value={signTemp}
                                label="Zodiac sign"
                                labelId="zodiac"
                                onChange={(e: SelectChangeEvent) => setSignTemp(e.target.value)}
                                fullWidth>
                                    {zodiacSigns.map((signs: Zodiac, idx: number) => { return(<MenuItem key={idx} value={signs.sign}>{signs.sign}</MenuItem>) })}
                            </Select>
                        </FormControl>
                            {Boolean(errorMsgs.sign)
                                ? <Typography color="error">{errorMsgs.sign}</Typography>
                                : null
                            }

                        <Button size="small" onClick={() => {setInputChange(false); inputHandler()}}>or simply pick your birthdate</Button>
                        </>
                }
                
                <Button type="submit" variant="contained" size="large" color="secondary" fullWidth sx={{marginTop:"10px", borderRadius:"30px"}}>Let’s Begin!</Button>
                
            </form>
            
        </Container>
    )
}

export default Onboarding